<template>
  <div class="card">
    <div class="card-body">
      <div>
        <h4 class="d-inline-block">Data Lengkap Pendaftaran</h4>
        <!-- <button
          type="button"
          class="btn btn-success float-right"
          data-toggle="modal"
          data-target="#Proposal"
        >
          Edit
        </button> -->
      </div>
      <!-- The Modal -->
      <div class="modal" id="Proposal">
        <div class="modal-dialog">
          <div class="modal-content">
            <!-- Modal Header -->
            <div class="modal-header">
              <h4 class="modal-title">
                <svg
                  width="24"
                  height="24"
                  viewBox="0 0 49 49"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <circle cx="24.5" cy="24.5" r="24.5" fill="#9DC2FF" />
                  <path
                    d="M27.5 12H17.5C16.837 12 16.2011 12.2634 15.7322 12.7322C15.2634 13.2011 15 13.837 15 14.5V34.5C15 35.163 15.2634 35.7989 15.7322 36.2678C16.2011 36.7366 16.837 37 17.5 37H32.5C33.163 37 33.7989 36.7366 34.2678 36.2678C34.7366 35.7989 35 35.163 35 34.5V19.5L27.5 12Z"
                    stroke="white"
                    stroke-width="3"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  />
                  <path
                    d="M27.5 12V19.5H35"
                    stroke="white"
                    stroke-width="3"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  />
                  <path
                    d="M25 32V24.5"
                    stroke="white"
                    stroke-width="3"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  />
                  <path
                    d="M21.25 28.25H28.75"
                    stroke="white"
                    stroke-width="3"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  />
                </svg>
                Edit Data Seminar Proposal
              </h4>
              <button type="button" class="close" data-dismiss="modal">
                &times;
              </button>
            </div>

            <!-- Modal body -->
            <div class="modal-body">
              <div class="form-group text-dark">
                <label for="fileSkripsi"
                  >File Sidang Skripsi
                  <small> (File pdf max file 10MB)</small></label
                >
                <input type="file" class="form-control h-25" id="fileSkripsi" />
              </div>
              <div class="form-group text-dark">
                <label for="buktiPembimbing1" class="w-100">
                  Foto Bukti Pembimbing 1
                  <small> (Foto PNG, JPG dan JPEG max foto 10MB)</small></label
                >
                <input
                  type="file"
                  class="form-control h-25"
                  id="buktiPembimbing1"
                />
              </div>
              <div class="form-group text-dark">
                <label for="buktiPembimbing2" class="w-100"
                  >Foto Bukti Pembimbing 2
                  <small> (Foto PNG, JPG dan JPEG max foto 10MB)</small></label
                >
                <input
                  type="file"
                  class="form-control h-25"
                  id="buktiPembimbing2"
                />
              </div>
            </div>

            <!-- Modal footer -->
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-outline-secondary"
                data-dismiss="modal"
              >
                Batal
              </button>
            </div>
          </div>
        </div>
      </div>
      <!-- batas modal -->
      <br />
      <div class="row">
        <p class="col-5 col-md-3">NPM</p>
        <p class="col-7 col-md-9">{{ getDatathesis.npm }}</p>
      </div>
      <div class="row">
        <p class="col-5 col-md-3">Nama Lengkap</p>
        <p class="col-7 col-md-9">{{ getDatathesis.full_name }}</p>
      </div>
      <div class="row">
        <p class="col-5 col-md-3">Program Studi</p>
        <p class="col-7 col-md-9">{{ getDataProfile.name_ina }}</p>
      </div>
      <div class="row">
        <p class="col-5 col-md-3">Jenis Kelamin</p>
        <p class="col-7 col-md-9" v-if="getDataProfile.gender === 'Male'">
          Laki-laki
        </p>
        <p
          class="col-7 col-md-9"
          v-else-if="getDataProfile.gender === 'Female'"
        >
          Perempuan
        </p>
      </div>
      <div class="row">
        <p class="col-5 col-md-3">Tempat, Tanggal Lahir</p>
        <p class="col-7 col-md-9">
          {{ getDataProfile.place_birth + ", " + getDataProfile.date_of_birth }}
        </p>
      </div>
      <div class="row">
        <p class="col-5 col-md-3">Nomer HP</p>
        <p class="col-7 col-md-9">{{ getDataProfile.handphone_number }}</p>
      </div>
      <div class="row">
        <p class="col-5 col-md-3">Judul Seminar Proposal (ID)</p>
        <p class="col-7 col-md-9">{{ getDatathesis.title_id }}</p>
      </div>
      <div class="row">
        <p class="col-5 col-md-3">Judul Seminar Propsal (EN)</p>
        <p class="col-7 col-md-9">{{ getDatathesis.title_en }}</p>
      </div>
      <div class="row">
        <p class="col-5 col-md-3">Dosen Pembimbing 1</p>
        <p class="col-7 col-md-9">{{ getDospem.full_name }}</p>
      </div>

      <div class="row">
        <p class="col-5 col-md-3">Dosen Pembimbing 2</p>
        <p class="col-7 col-md-9">{{ getDospem2.full_name }}</p>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DaftarSkripsiFilled",
  computed: {
    ...mapGetters({
      getDatathesis: "daftarskripsi/getDataSkripsi",
      getDospem: "dospemskripsi/getDospem",
      getDospem2: "dospemskripsi/getDospem2",
      getDataProfile: "profile/profile",
    }),
  },
  methods: {
    ...mapActions({
      daftarskp: "daftarskripsi/skripsi",
      dospemskp: "dospemskripsi/dospemskripsi",
      profile: "profile/profile",
    }),
    getDaftarSkrip() {
      this.daftarskp();
    },
    getDospemSkrip() {
      this.dospemskp();
    },
    getProfile() {
      this.profile();
    },
  },
  mounted() {
    this.getDaftarSkrip();
    this.getDospemSkrip();
    this.getProfile();
  },
};
</script>
