<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "DaftarYudisium",
  inject: ["notyf"],
  data() {
    return {
      formJudul: {
        title_id: "",
        title_en: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      getDospem: "dospemskripsi/getDospem",
      getDospem2: "dospemskripsi/getDospem2",
      getDaftar: "daftarskripsi/getDataSkripsi",
      getYudisium: "statusDaftar/getStatusYudisium",
    }),
  },
  methods: {
    ...mapActions({
      dospemskp: "dospemskripsi/dospemskripsi",
      daftarskp: "daftarskripsi/skripsi",
      statusYudisium: "statusDaftar/getReport",
    }),
    getDospemSkrip() {
      this.dospemskp();
    },
    getDataJudul() {
      this.daftarskp();
    },
    getDaftarYidisum() {
      this.statusYudisium();
    },
    async submit() {
      try {
        let EditJudul = {
          title_id: this.formJudul.title_id,
          title_en: this.formJudul.title_en,
        };
        let response = await axios.put(
          "/siakad/update-thesis/key=" + this.getDaftar.id,
          EditJudul
        );
        // this.getDaftarSkrip();
        // console.log(EditJudul);
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },
  mounted() {
    this.getDospemSkrip();
    this.getDataJudul();
    this.getDaftarYidisum();
    this.formJudul.title_id = this.getDaftar.title_id;
    this.formJudul.title_en = this.getDaftar.title_en;
  },
};
</script>

