<template>
  <div class="col-md-12 mb-3">
    <!-- <h1 class="h1 mb-3">
      Skripsi
      <button class="btn btn-md btn-success btn-sm float-right">
        Riwayat Pendaftaran Anda
      </button>
    </h1> -->
    <div class="row">
      <div class="col-12 col-lg-12">
        <div class="accordion" id="accordionExample">
          <div id="info" class="card">
            <a
              href="#"
              data-toggle="collapse"
              data-target="#collapseOne"
              aria-expanded="true"
              aria-controls="collapseOne"
              class="card-title mb-0"
            >
              <div class="card-header" id="headingOne">
                <h4 class="text-ungu">
                  Data Seminar Terbuka
                  <i class="fas fa-chevron-down float-right"></i>
                </h4>
              </div>
            </a>
            <div
              id="collapseOne"
              class="collapse show"
              aria-labelledby="headingOne"
              data-parent="#accordionExample"
            >
              <div class="card-body">
                <form @submit.prevent="submit">
                  <div class="form-group text-dark">
                    <label for="jdllid">Judul Seminar Terbuka (ID) </label>
                    <textarea
                      type="text"
                      class="form-control"
                      id="jdllid"
                      placeholder="Judul Seminar Terbuka (ID)"
                      v-model="formJudul.title_id"
                    />
                  </div>
                  <div class="form-group text-dark">
                    <label for="jdlen">Judul Seminar Terbuka (EN) </label>
                    <textarea
                      type="text"
                      class="form-control"
                      id="jdlen"
                      placeholder="Judul Seminar Terbuka (EN)"
                      v-model="formJudul.title_en"
                    />
                  </div>
                  <div class="form-group text-dark">
                    <label for="dosen">Dosen Pembimbing 1</label>
                    <input
                      type="text"
                      class="form-control"
                      :value="getDospem.full_name"
                      id="dosen"
                      placeholder=""
                      disabled
                    />
                  </div>
                  <div class="form-group text-dark">
                    <label for="dpem">Dosen Pembimbing 2</label>
                    <input
                      type="text"
                      class="form-control"
                      :value="getDospem2.full_name"
                      id="dpem"
                      placeholder=""
                      disabled
                    />
                  </div>
                  <button
                    type="submit"
                    class="btn btn-primary btn-success btn-sm ml-0"
                  >
                    Simpan
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="card">
      <div class="card-body">
        <!-- <div class="form-row">
          <div class="form-group col-md-2">
            <label class="text-dark">Tipe Pendaftaran</label>
          </div>
          <div class="form-group col-md-5">
            <select class="form-control" id="exampleFormControlSelect1">
              <option>Pilih Pendaftaran</option>
              <option>Pendaftaran Seminar Proposal Skripsi</option>
              <option>Pendaftaran Seminar Terbuka Skripsi</option>
              <option>Pendaftaran Sidang Skripsi</option>
              <option>Pendaftaran Yudisium</option>
            </select>
          </div>
        </div> -->
        <div class="form-group text-dark">
          <label for="FileSeminar"
            >File Seminar Terbuka<small>
              (File harus pdf max file 10MB)</small
            ></label
          >
          <input
            type="file"
            class="form-control col-12 col-md-5 h-25"
            id="FileSeminar"
          />
        </div>
        <div class="form-group text-dark">
          <label for="fileTranskrip"
            >File Transkrip Nilai<small>
              (File harus pdf max file 10MB)</small
            ></label
          >
          <input
            type="file"
            class="form-control col-12 col-md-5 h-25"
            id="fileTranskrip"
          />
        </div>
        <div class="form-group text-dark">
          <label for="fileKartu"
            >File Kartu Bimbingan<small>
              (File harus pdf max file 10MB)</small
            ></label
          >
          <input
            type="file"
            class="form-control col-12 col-md-5 h-25"
            id="fileKartu"
          />
        </div>
        <div class="form-group text-dark">
          <label for="fotoBukti1"
            >Foto Bukti Chat Pembimbing 1<small>
              (Gambar harus PNG, JPG dan JPEG max size 10MB)</small
            ></label
          >
          <input
            type="file"
            class="form-control col-12 col-md-5 h-25"
            id="fotoBukti1"
          />
        </div>
        <div class="form-group text-dark">
          <label for="fotoBukti2"
            >Foto Bukti Chat Pembimbing 2<small>
              (Gambar harus PNG, JPG dan JPEG max size 10MB)</small
            ></label
          >
          <input
            type="file"
            class="form-control col-12 col-md-5 h-25"
            id="fotoBukti2"
          />
        </div>
        <div class="form-group text-dark">
          <label for="BuktiPenguji1"
            >Foto Bukti Chat Penguji 1<small>
              (Gambar harus PNG, JPG dan JPEG max size 10MB)</small
            ></label
          >
          <input
            type="file"
            class="form-control col-12 col-md-5 h-25"
            id="BuktiPenguji1"
          />
        </div>
        <div class="form-group text-dark">
          <h5>Tanda Tangan Mahasiswa</h5>
          <p>
            <small
              >Klik gambar untuk menginput tanda tangan (jika tanda tangan belum
              ada)</small
            >
          </p>
          <div class="row">
            <p class="col-10 col-md-3">
              <textarea class="form-control rounded-0" id="ttd" rows="6">
              </textarea>
            </p>
            <div>
              <button class="btn btn-primary btn-success btn-sm mt-5">
                <i class="fas fa-sync-alt"></i>
              </button>
            </div>
          </div>
        </div>
        <button type="submit" class="btn btn-primary btn-success btn-sm ml-0">
          DAFTAR SEMINAR TERBUKA
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DaftarTerbuka",
  inject: ["notyf"],
  data() {
    return {
      formJudul: {
        title_id: "",
        title_en: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      username: "auth/users",
      getDospem: "dospemskripsi/getDospem",
      getDospem2: "dospemskripsi/getDospem2",
      getDaftar: "daftarskripsi/getDataSkripsi",
      getTerbuka: "statusDaftar/getStatusTerbuka",
    }),
  },
  methods: {
    ...mapActions({
      dospemskp: "dospemskripsi/dospemskripsi",
      daftarskp: "daftarskripsi/skripsi",
      statusTerbuka: "statusDaftar/getReport",
    }),
    getDospemSkrip() {
      this.dospemskp();
    },
    getDataJudul() {
      this.daftarskp();
    },
    getDataDaftarTerbuka() {
      this.statusTerbuka();
    },
    async submit() {
      try {
        let EditJudul = {
          title_id: this.formJudul.title_id,
          title_en: this.formJudul.title_en,
        };
        let response = await axios.put(
          "/siakad/update-thesis/key=" + this.getDaftar.id,
          EditJudul
        );
        // this.getDaftarSkrip();
        // console.log(EditJudul);
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },
  mounted() {
    this.getDospemSkrip();
    this.getDataJudul();
    this.getDataDaftarTerbuka();
    this.formJudul.title_id = this.getDaftar.title_id;
    this.formJudul.title_en = this.getDaftar.title_en;
    // console.log(this.getDaftar.title_id);
  },
};
</script>

