<template>
  <!-- <div class="col-md-12 mb-3">
    <h1 class="h1 mb-3">Skripsi</h1> -->

  <div class="card">
    <div class="card-body">
      <p>
        <span class="text-danger">*</span> Jika info dengan tanda ini salah,
        maka bisa diperbaiki melalui pemutakhiran data
      </p>
      <form @submit.prevent="submit">
        <div class="form-group text-dark">
          <label for="npmmhs">NPM</label>
          <input
            type="text"
            class="form-control"
            id="npmmhs"
            :value="getDataProfile.npm"
            placeholder="NPM"
            disabled
          />
        </div>
        <div class="form-group text-dark">
          <label for="namamhs">Nama Lengkap</label>
          <input
            type="text"
            class="form-control"
            id="namamhs"
            :value="getDataProfile.full_name"
            placeholder="Nama Lengkap"
            disabled
          />
        </div>
        <div class="form-group text-dark">
          <label for="prodi">Program Studi</label>
          <input
            type="text"
            class="form-control"
            id="prodi"
            :value="getDataProfile.name_ina"
            placeholder="Program Studi"
            disabled
          />
        </div>
        <div class="form-group text-dark">
          <label for="jenis"
            >Jenis Kelamin <span class="text-danger">*</span></label
          >
          <input
            v-if="getDataProfile.gender === 'Male'"
            type="text"
            class="form-control"
            id="jenis"
            value="Laki-laki"
            placeholder="Jenis Kelamin"
            disabled
          />
          <input
            v-else-if="getDataProfile.gender === 'Female'"
            type="text"
            class="form-control"
            id="jkp"
            value="Perempuan"
            placeholder="Jenis Kelamin"
            disabled
          />
        </div>
        <div class="form-group text-dark">
          <label for="tempattgl"
            >Tempat, Tanggal Lahir <span class="text-danger">*</span></label
          >
          <input
            type="text"
            class="form-control"
            id="tempattgl"
            :value="
              getDataProfile.place_birth + ', ' + getDataProfile.date_of_birth
            "
            placeholder="Tempat, Tanggal Lahir"
            disabled
          />
        </div>
        <div class="form-group text-dark">
          <label for="nohape"
            >Nomer Hp <span class="text-danger">*</span></label
          >
          <input
            type="text"
            class="form-control"
            id="nohape"
            :value="getDataProfile.handphone_number"
            placeholder="Nomer Hp"
            disabled
          />
        </div>
        <div class="form-group text-dark">
          <label for="indonesia">Judul Seminar Proposal (ID) </label>
          <textarea
            type="text"
            class="form-control"
            id="indonesia"
            v-model="form.title_id"
          />
        </div>
        <div class="form-group text-dark">
          <label for="judulen">Judul Seminar Proposal (EN) </label>
          <textarea
            type="text"
            class="form-control"
            id="judulen"
            v-model="form.title_en"
            placeholder="Judul Seminar Proposal (EN)"
          />
        </div>
        <div class="form-group text-dark">
          <label for="dosenpembimbing1">Dosen Pembimbing 1</label>
          <input
            type="text"
            class="form-control"
            id="dosenpembimbing1"
            placeholder="Dosen Pembimbing 1"
            :value="getDospem.full_name"
            disabled
          />
        </div>
        <div class="form-group text-dark">
          <label for="dosenpembimbing2">Dosen Pembimbing 2</label>
          <input
            type="text"
            class="form-control"
            id="dosenpembimbing2"
            placeholder=""
            :value="getDospem2.full_name"
            disabled
          />
        </div>
        <button type="submit" class="btn btn-primary btn-success ml-0">
          Daftar Sekarang
        </button>
        <br />
      </form>
    </div>
  </div>
  <!-- </div> -->
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DataSkripsi",
  inject: ["notyf"],
  data() {
    return {
      form: {
        username: null,
        guide1_id: null,
        guide2_id: null,
        title_id: null,
        title_en: null,
      },
    };
  },
  computed: {
    ...mapGetters({
      username: "auth/users",
      getDospem: "dospemskripsi/getDospem",
      getDospem2: "dospemskripsi/getDospem2",
      getDaftar: "daftarskripsi/getDataSkripsi",
      getDataProfile: "profile/profile",
    }),
  },
  methods: {
    ...mapActions({
      dospemskp: "dospemskripsi/dospemskripsi",
      daftarskp: "daftarskripsi/skripsi",
      profile: "profile/profile",
    }),
    getDospemSkrip() {
      this.dospemskp();
    },
    getDaftarSkrip() {
      this.daftarskp();
    },
    getProfile() {
      this.profile();
    },

    async submit() {
      try {
        let dataSkripsi = {
          username: this.username.username,
          guide1_id: this.form.guide1_id,
          guide2_id: this.form.guide2_id,
          title_id: this.form.title_id,
          title_en: this.form.title_en,
        };
        console.log(dataSkripsi);
        let response = await axios.post("/siakad/send-thesis", dataSkripsi);
        // console.log(response);
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },
  mounted() {
    this.getDospemSkrip();
    this.getDaftarSkrip();
    this.getProfile();
    this.form.guide1_id = this.getDospem.id;
    this.form.guide2_id = this.getDospem2.id;
  },
};
</script>

