<template>
  <div class="col-md-12 mb-3">
    <div class="proposal">
      <div class="row">
        <div class="col-12 col-lg-12">
          <div class="accordion" id="accordionExample">
            <div id="info" class="card">
              <a
                href="#"
                data-toggle="collapse"
                data-target="#collapseOne"
                aria-expanded="true"
                aria-controls="collapseOne"
                class="card-title mb-0"
              >
                <div class="card-header" id="headingOne">
                  <h4 class="text-ungu">
                    Data Seminar Proposal
                    <i class="fas fa-chevron-down float-right"></i>
                  </h4>
                </div>
              </a>
              <div
                id="collapseOne"
                class="collapse show"
                aria-labelledby="headingOne"
                data-parent="#accordionExample"
              >
                <div class="card-body">
                  <form @submit.prevent="submit">
                    <div class="form-group text-dark">
                      <label>Judul Seminar Proposal (ID) </label>
                      <textarea
                        v-model="formJudul.title_id"
                        type="text"
                        class="form-control"
                        required
                      />
                    </div>
                    <div class="form-group text-dark">
                      <label>Judul Seminar Proposal (EN) </label>
                      <textarea
                        v-model="formJudul.title_en"
                        type="text"
                        class="form-control"
                        required
                      />
                    </div>
                    <div class="form-group text-dark">
                      <label for="1">Dosen Pembimbing 1</label>
                      <input
                        type="text"
                        class="form-control"
                        id="1"
                        :value="getDospem.full_name"
                        disabled
                      />
                    </div>
                    <div class="form-group text-dark">
                      <label for="2">Dosen Pembimbing 2</label>
                      <input
                        type="text"
                        class="form-control"
                        :value="getDospem2.full_name"
                        id="2"
                        placeholder=""
                        disabled
                      />
                    </div>
                    <button
                      type="submit"
                      class="btn btn-primary btn-success btn-sm ml-0"
                    >
                      Simpan
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card" v-if="getProposal === 0">
        <div class="card-body">
          <!-- <div class="form-row">
          <div class="form-group col-md-2">
            <label class="text-dark">Tipe Pendaftaran</label>
          </div>
          <div class="form-group col-md-5">
            <select class="form-control" id="exampleFormControlSelect1">
              <option>Pilih Pendaftaran</option>
              <option>Pendaftaran Seminar Proposal Skripsi</option>
              <option>Pendaftaran Seminar Terbuka Skripsi</option>
              <option>Pendaftaran Sidang Skripsi</option>
              <option>Pendaftaran Yudisium</option>
            </select>
          </div>
        </div> -->
          <div class="form-group text-dark">
            <label for="fileproposal"
              >File Seminar Proposal<small>
                (File harus pdf max file 10MB)</small
              ></label
            >
            <input
              type="file"
              class="form-control col-12 col-md-5 h-25"
              id="fileproposal"
            />
          </div>
          <div class="form-group text-dark">
            <label for="BuktiChat1"
              >Foto Bukti Chat Pembimbing 1<small>
                (Gambar harus PNG, JPG dan JPEG max size 10MB)</small
              ></label
            >
            <input
              type="file"
              class="form-control col-12 col-md-5 h-25"
              id="BuktiChat1"
            />
          </div>
          <div class="form-group text-dark">
            <label for="buktiChat2"
              >Foto Bukti Chat Pembimbing 2<small>
                (Gambar harus PNG, JPG dan JPEG max size 10MB)</small
              ></label
            >
            <input
              type="file"
              class="form-control col-12 col-md-5 h-25"
              id="buktiChat2"
            />
          </div>
          <div class="form-group text-dark">
            <h5>Tanda Tangan Mahasiswa</h5>
            <p>
              <small
                >Klik gambar untuk menginput tanda tangan (jika tanda tangan
                belum ada)</small
              >
            </p>
            <div class="row">
              <p class="col-10 col-md-3">
                <textarea class="form-control" id="ttd" rows="6"> </textarea>
              </p>
              <div>
                <button class="btn btn-primary btn-success btn-sm mt-5">
                  <i class="fas fa-sync-alt"></i>
                </button>
              </div>
            </div>
          </div>
          <button type="submit" class="btn btn-primary btn-success btn-sm ml-0">
            DAFTAR SEMINAR PROPOSAL {{ getProposal }}
          </button>
        </div>
      </div>
    </div>
    <div class="editProposal"></div>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DaftarProposal",
  inject: ["notyf"],
  data() {
    return {
      formJudul: {
        title_id: "",
        title_en: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      username: "auth/users",
      getDospem: "dospemskripsi/getDospem",
      getDospem2: "dospemskripsi/getDospem2",
      getDaftar: "daftarskripsi/getDataSkripsi",
      getProposal: "statusDaftar/getStatusProposal",
    }),
  },
  methods: {
    ...mapActions({
      dospemskp: "dospemskripsi/dospemskripsi",
      statusProposal: "statusDaftar/getReport",
      daftarskp: "daftarskripsi/skripsi",
    }),
    getDospemSkrip() {
      this.dospemskp();
    },
    getDaftarProposal() {
      this.statusProposal();
    },
    getDaftarSkrip() {
      this.daftarskp();
    },
    async submit() {
      try {
        let EditJudul = {
          title_id: this.formJudul.title_id,
          title_en: this.formJudul.title_en,
        };
        let response = await axios.put(
          "/siakad/update-thesis/key=" + this.getDaftar.id,
          EditJudul
        );
        // this.getDaftarSkrip();
        // console.log(EditJudul);
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },
  mounted() {
    this.getDospemSkrip();
    this.getDaftarProposal();
    this.getDaftarSkrip();
    this.formJudul.title_id = this.getDaftar.title_id;
    this.formJudul.title_en = this.getDaftar.title_en;
    // console.log(this.getDaftar.title_id);
  },
};
</script>

