<template>
  <div class="container-fluid p-0">
    <div class="row">
      <div class="col-12 col-md-12">
        <div id="smartwizard">
          <ul class="nav">
            <li>
              <a class="nav-link" href="#step-1"> Input Data </a>
            </li>
            <li>
              <a class="nav-link" href="#step-2"> Seminar Proposal </a>
            </li>
            <li>
              <a class="nav-link" href="#step-3"> Seminar Terbuka </a>
            </li>
            <li>
              <a class="nav-link" href="#step-4"> Sidang Skripsi </a>
            </li>
            <li>
              <a class="nav-link" href="#step-5"> Yudisium </a>
            </li>
          </ul>

          <div class="tab-content">
            <div id="step-1" class="tab-pane" role="tabpanel">
              <DataSkripsi v-if="getStatusSkripsi === 0" />
              <DaftarSkripsiFilled v-else-if="getStatusSkripsi === 1" />
            </div>
            <div id="step-2" class="tab-pane" role="tabpanel">
              <DaftarProposal />
            </div>
            <div id="step-3" class="tab-pane" role="tabpanel">
              <DaftarTerbuka />
            </div>
            <div id="step-4" class="tab-pane" role="tabpanel">
              <DaftarSkripsi />
            </div>
            <div id="step-5" class="tab-pane" role="tabpanel">
              <DaftarYudisium />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DataSkripsi from "../../components/pendaftaran/skripsi/skripsistep/DataSkripsi.vue";
import DaftarProposal from "../../components/pendaftaran/skripsi/skripsistep/DaftarProposal.vue";
import DaftarTerbuka from "../../components/pendaftaran/skripsi/skripsistep/DaftarTerbuka.vue";
import DaftarSkripsi from "../../components/pendaftaran/skripsi/skripsistep/DaftarSkripsi.vue";
import DaftarYudisium from "../../components/pendaftaran/skripsi/skripsistep/DaftarYudisium.vue";
import DaftarSkripsiFilled from "../../components/pendaftaran/skripsi/skripsistep/DaftarSkripsiFilled.vue";
import { mapActions, mapGetters } from "vuex";
import "smartwizard";
import "smartwizard/dist/css/smart_wizard_arrows.css";
export default {
  name: "Skripsi",
  mounted() {
    $("title").html("Skripsi | STTBANDUNG");
    $("#smartwizard").smartWizard({
      selected: 0, // Initial selected step, 0 = first step
      theme: "arrows", // theme for the wizard, related css need to include for other than default theme
      justified: true, // Nav menu justification. true/false
      darkMode: false, // Enable/disable Dark Mode if the theme supports. true/false
      autoAdjustHeight: false, // Automatically adjust content height
      cycleSteps: false, // Allows to cycle the navigation of steps
      backButtonSupport: true, // Enable the back button support
      enableURLhash: true, // Enable selection of the step based on url hash
      transition: {
        animation: "fade", // Effect on navigation, none/fade/slide-horizontal/slide-vertical/slide-swing
        speed: "400", // Transion animation speed
        easing: "", // Transition animation easing. Not supported without a jQuery easing plugin
      },
      toolbarSettings: {
        toolbarPosition: "bottom", // none, top, bottom, both
        toolbarButtonPosition: "right", // left, right, center
        showNextButton: true, // show/hide a Next button
        showPreviousButton: true, // show/hide a Previous button
        toolbarExtraButtons: [], // Extra buttons to show on toolbar, array of jQuery input/buttons elements
      },
      anchorSettings: {
        anchorClickable: true, // Enable/Disable anchor navigation
        enableAllAnchors: false, // Activates all anchors clickable all times
        markDoneStep: true, // Add done state on navigation
        markAllPreviousStepsAsDone: true, // When a step selected by url hash, all previous steps are marked done
        removeDoneStepOnNavigateBack: false, // While navigate back done step after active step will be cleared
        enableAnchorOnDoneStep: true, // Enable/Disable the done steps navigation
      },
      keyboardSettings: {
        keyNavigation: true, // Enable/Disable keyboard navigation(left and right keys are used if enabled)
        keyLeft: [37], // Left key code
        keyRight: [39], // Right key code
      },
      lang: {
        // Language variables for button
        next: "Next",
        previous: "Previous",
      },
      disabledSteps: [], // Array Steps disabled
      errorSteps: [], // Highlight step with errors
      hiddenSteps: [], // Hidden steps
    });
    this.getStatusDaftar();
    // console.log(this.getStatus);
  },
  components: {
    DataSkripsi,
    DaftarProposal,
    DaftarTerbuka,
    DaftarSkripsi,
    DaftarYudisium,
    DaftarSkripsiFilled,
  },
  computed: {
    ...mapGetters({
      getStatusSkripsi: "statusDaftar/getStatusSkripsi",
    }),
  },
  methods: {
    ...mapActions({
      status: "statusDaftar/getReport",
    }),
    getStatusDaftar() {
      this.status();
    },
  },
};
</script>

<style>
.sw-theme-arrows {
  border: none !important;
}
</style>